.rows {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.row {
  display: flex;
  border-bottom: 1px solid #ccc;
  flex-shrink: 0;
}

.title {
  display: flex;
  align-items: center;
  margin: 0;
  color: black;
  font-size: 12px;
  font-weight: bold;
  padding: 4px 4px 4px 8px;
  border-left: 1px solid #ccc;
  justify-content: center;
}

.value {
  display: flex;
  align-items: center;
  margin: 0;
  background-color: white;
  font-size: 12px;
  padding: 4px 4px 4px 8px;
  border-left: 1px solid #ccc;
  justify-content: center;
}

.column1 {
  flex-grow: 0;
  flex-basis: 400px;
  min-width: 200px;
}
.column2 {
  flex-grow: 0;
  flex-basis: 60px;
  min-width: 48px;
}

.column3 {
  flex-grow: 0;
  flex-basis: 90px;
  flex-shrink: 0;
}

.column4 {
  flex-grow: 0;
  flex-basis: 80px;
  flex-shrink: 0;
}

.column5 {
  flex-grow: 0;
  flex-basis: 50px;
  flex-shrink: 0;
}

.column6 {
  flex-grow: 0;
  flex-basis: 200px;
}

.text__left {
  justify-content: flex-start;
  text-align: start;
}
