.container {
  display: flex;
  position: fixed;
  top: 0;
  flex-shrink: 0;
  flex-direction: column;
  border-left: 5px solid #c9d2de;

  width: 235px;
  height: 100vh;
  overflow: auto;
}

@media (max-width: 1100px) {
  .container {
    display: none;
  }
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
  0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  background-color: white;
  height: calc(100vh - 80px);
}

.navigation__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
}

.navigation__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
}

.navigation__text {
  margin: 0;
  padding-left: 4px;
  font-size: 14px;
}

.navigation__item__icon {
  display: flex;
  flex-direction: row;
  align-items: center;
  transition: all 0.3s ease-in-out;
  margin-left: 8px;
  padding-top: 8px;
  padding-bottom: 8px;
  color: #000;
  text-decoration: none;
}

.navigation__item__icon:hover {
  opacity: 0.5;
  color: #4393c5;
}
