.rows {
  display: flex;
  flex-direction: column;
  padding: 8px;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  background-color: #f3f4f8;
  padding: 4px;
}

.row_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
}

.column__title {
  display: flex;
  flex-grow: 0;
  flex-basis: 200px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 4px;
  color: black;
  font-size: 12px;
  min-width: 140px;
}

.title {
  flex-grow: 0;
  flex-basis: 140px;
  margin: 0;
  color: #4393c5;
  font-size: 12px;
  min-width: 90px;
}

.value {
  display: flex;
  flex-grow: 0;
  flex-basis: 200px;
  justify-content: center;
  align-items: center;
  margin: 0;
  background-color: white;
  padding: 8px;
  font-size: 12px;
}

@media (max-width: 600px) {
  .title {
    min-width: 80px;
  }

  .value {
    height: 28px;
    min-width: 80px;
    padding: 4px;
  }

  .column__title {
    min-width: 80px;
  }
}
