.main {
  display: flex;
  margin-right: 8px;
  margin-left: 248px;
  height: calc(100vh - 82px);
  gap: 8px;
}

.item__header {
  display: flex;
  align-items: center;
  padding: 8px;
}

.item__title {
  margin: 0 0 0 8px;
  font-weight: 400;
  font-size: 18px;
}

.main__column {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

@media (max-width: 1919px) {
  .main {
    flex-direction: column;
  }
}

@media (max-width: 1100px) {
  .main {
    margin: 8px auto;
  }
}

@media (max-width: 690px) {
  .main {
    margin: 0;
  }
}
