.user {
  display: flex;
  align-items: center;
  padding-top: 16px;
  padding-bottom: 16px;
  max-width: 235px;
  flex-grow: 0;
}

.user__image {
  margin-right: 8px;
  margin-left: 8px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.user__info {
  display: flex;
  flex-direction: column;
  width: 72%;
}

.name_container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
}

.user__buttons {
  margin-left: 8px;
}

.user__name {
  display: flex;
  margin: 0;
  font-weight: 300;
  font-size: 12px;
  text-align: left;
}

.user__position {
  margin: 0;
  padding-top: 4px;
  font-weight: 200;
  font-size: 12px;
  text-align: left;
}

.user__button {
  position: relative;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
}

.user__button:hover {
  opacity: 0.5;
  color: #4393c5;
}

.user__button__count {
  display: flex;
  position: absolute;
  top: -10px;
  right: -5px;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #c71d29;
  width: 14px;
  height: 14px;
  color: white;
  font-weight: 200;
  font-size: 10px;
}
