.header {
    display: flex;
    align-items: center;
    padding-right: 16px;
    margin-left: 243px;
}

.header__title {
    font-size: 20px;
    font-weight: 400;
    margin: 0;
    padding: 16px;
}

.header__actions {
    display: flex;
    align-items: center;
    margin-left: auto;
}

.header__actions__item {
    margin-left: 16px;
    color: black;
    text-decoration: underline;
    font-size: 12px;
    transition: all 0.3s ease-in-out;
}

.header__actions__item:hover {
    color: #4393c5;
    opacity: .5;
}

.header__actions__item__icon {
    margin-right: 8px;
    height: 24px;
    object-fit: cover;
}

@media (max-width: 1100px) {
  .header {
    margin-left: 0;
  }

  .header__title {
    font-size: 18px;
    padding: 8px;
  }
}

@media (max-width: 690px) {
  .header__actions {
    display: none;
  }

  .header {
    justify-content: center;
  }
}
