.generalInfo {
  display: flex;
  padding: 8px;
  background-color: white;
  gap: 8px;
}

.generalInfo__item {
  background-color: #f3f4f8;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.generalInfo__item__horizontal {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  background-color: #f3f4f8;
  padding: 8px;
}

.generalInfo__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 160px;
}

.row {
  display: flex;
  align-items: center;
  gap: 4px;
}

.generalInfo__title {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  min-height: 80px;
  color: black;
  margin: 4px 8px 8px 8px ;
  font-size: 16px;
  width: 100px;
}

.generalInfo__text {
  color: #4393c5;
  font-size: 12px;
  margin: 0;
}

.text_left {
  text-align: left;
}

.row__text_green {
  color: #498d44;
  font-size: 12px;
  margin: 0;
}

.row__text_orange {
  color: #f2ab2c;
  font-size: 12px;
  margin: 0;
}

.status {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  padding: 4px;
  width: 200px;
  height: 30px;
}

.row__text {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  width: 200px;
  margin: 0;
  background-color: white;
  padding: 4px;
  height: 30px;
}

.icon_title {
  display: flex;
  align-items: center;
  width: 130px;
}

.column__title {
  margin: 0;
  color: black;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
}