.container {
  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  text-align: center;
  background-color: white;
  max-width: 830px;
  overflow-x: auto;
}

.item__header {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ccc;
  padding: 8px;
}

.item__title {
  margin: 0 0 0 8px;
  font-weight: 400;
  font-size: 18px;
}

.item__add {
  margin-left: auto;
  color: white;
  background: #227be3;
  font-size: 12px;
  border: none;
  padding: 8px;
  border-radius: 5px;
  transition: opacity 0.3s ease-in-out;
  cursor: pointer;
}

.item__add:hover {
  opacity: .6;
}

@media (max-width: 690px) {
  .item__title {
    font-size: 16px;
  }
}